import React from 'react'
import s from './WinnersBlock.module.css'

function WinnersBlock(props) {

    return (
        <div className={s.WinnersBlock}>
            <div className={s.Lable}>{props.name}</div>
                <div className={s.table}>
                    <div className={s.row1}>
                        <div className={s.col1}><span className={s.name}></span></div>
                        <div className={s.col2}><span className={s.name}></span></div>
                        <div className={s.col3}><span className={s.name}>. </span></div>
                    </div>
                    <div className={s.row2}>
                        <div className={s.colum1}><span className={s.name}>1 </span></div>
                        <div className={s.colum2}><span className={s.name}>{props.Data[0][0]}</span></div>
                        <div className={s.colum3}><span className={s.name}>{props.Data[0][1]}</span></div>
                    </div>
                    <div className={s.row3}>
                        <div className={s.colum1}><span className={s.name}>2 </span></div>
                        <div className={s.colum2}><span className={s.name}>{props.Data[1][0]}</span></div>
                        <div className={s.colum3}><span className={s.name}>{props.Data[1][1]}</span></div>
                    </div>
                    <div className={s.row4}>
                        <div className={s.colum1}><span className={s.name}>3 </span></div>
                        <div className={s.colum2}><span className={s.name}>{props.Data[2][0]}</span></div>
                        <div className={s.colum3}><span className={s.name}>{props.Data[2][1]}</span></div>
                    </div>
            </div>
        </div>
    );
}


export default WinnersBlock;